.nft-card {
  background-color: $very-dark-blue;
  height: 100%;
  width: 100%;
  min-width: 100%;
  min-height: 100vh;
  overflow: hidden;
  display: grid;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.nft-card__card,
.nft-card .card {
  padding: 24px;
  background-color: $dark-blue;
  width: 100%;
  max-width: 350px;
  border-radius: 15px;
  -webkit-box-shadow: 0px 25px 50px rgba(0,0,0,0.095);
          box-shadow: 0px 25px 50px rgba(0,0,0,0.095);
}
.nft-card__card__preview,
.nft-card .card__preview,
.nft-card__card .preview,
.nft-card .card .preview {
  width: 100%;
  height: 303px;
  border-radius: 15px;
  margin-bottom: 24px;
  background-image: url("/assets/images/artwork.webp");
  background-size: cover;
  background-position: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.nft-card__card__preview__banner,
.nft-card .card__preview__banner,
.nft-card__card .preview__banner,
.nft-card .card .preview__banner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $cyan;
  opacity: 0;
  cursor: pointer;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}
.nft-card__card__preview__eye,
.nft-card .card__preview__eye,
.nft-card__card .preview__eye,
.nft-card .card .preview__eye {
  top: 50%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 48px;
  opacity: 0;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
  pointer-events: none;
}
.nft-card__card__details,
.nft-card .card__details,
.nft-card__card .details,
.nft-card .card .details {
  display: grid;
  grid-template-areas: "title title" "description description" "price availability";
  grid-template-colums: repeat(2, 1fr);
  padding-bottom: 24px;
  border-bottom: 1px solid $grey;
}
.nft-card__card__details__title,
.nft-card .card__details__title,
.nft-card__card .details__title,
.nft-card .card .details__title {
  grid-area: title;
  color: $white;
  font-weight: 600;
  font-size: 22px;
  margin-bottom: 16px;
}
.nft-card__card__details__description,
.nft-card .card__details__description,
.nft-card__card .details__description,
.nft-card .card .details__description {
  grid-area: description;
  color: $soft-blue;
  font-size: 18px;
  margin-bottom: 24px;
  font-weight: 300;
}
.nft-card__card__details__price,
.nft-card .card__details__price,
.nft-card__card .details__price,
.nft-card .card .details__price {
  grid-area: price;
  color: $cyan;
  font-size: 15px;
  font-weight: 600;
}
.nft-card__card__details__price::before,
.nft-card .card__details__price::before,
.nft-card__card .details__price::before,
.nft-card .card .details__price::before {
  content: "";
  width: 11px;
  height: 16px;
  background: url("/assets/icons/icon-ethereum.svg") no-repeat;
  background-position: center;
  background-size: 100%;
  display: inline-block;
  margin-right: 4px;
  position: relative;
  top: 2px;
}
.nft-card__card__details__availability,
.nft-card .card__details__availability,
.nft-card__card .details__availability,
.nft-card .card .details__availability {
  grid-area: availability;
  color: $soft-blue;
  font-size: 15px;
  justify-self: end;
}
.nft-card__card__details__availability::before,
.nft-card .card__details__availability::before,
.nft-card__card .details__availability::before,
.nft-card .card .details__availability::before {
  content: "";
  width: 16px;
  height: 16px;
  background: url("/assets/icons/icon-clock.svg") no-repeat;
  background-position: center;
  background-size: 100%;
  display: inline-block;
  margin-right: 6px;
  position: relative;
  top: 2px;
}
.nft-card__card__artist,
.nft-card .card__artist,
.nft-card__card .artist,
.nft-card .card .artist {
  margin-top: 16px;
  color: $soft-blue;
  font-size: 15px;
  font-weight: 500;
}
.nft-card__card__artist::before,
.nft-card .card__artist::before,
.nft-card__card .artist::before,
.nft-card .card .artist::before {
  content: "";
  width: 33px;
  height: 33px;
  background: url("/assets/images/profile-picture.webp") no-repeat;
  background-position: center;
  background-size: 100%;
  display: inline-block;
  margin-right: 16px;
  position: relative;
  vertical-align: middle;
  margin-bottom: 4px;
}
.nft-card__card__artist__link,
.nft-card .card__artist__link,
.nft-card__card .artist__link,
.nft-card .card .artist__link {
  color: $white;
  cursor: pointer;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
}
.nft-card__card__artist__link:hover,
.nft-card .card__artist__link:hover,
.nft-card__card .artist__link:hover,
.nft-card .card .artist__link:hover {
  color: $cyan;
}
@media (max-width: 500px) {
  .nft-card__artist,
  .nft-card .artist {
    text-align: center;
  }
  .nft-card__artist::before,
  .nft-card .artist::before {
    content: "" !important;
    width: 0;
    height: 0;
    background: unset;
    display: none !important;
  }
}
